import { defineStore } from "pinia";

//
const playerSettingInfo = JSON.parse(window.localStorage.getItem('player-setting')) || {};

export const playerSettingStore = defineStore('playerSetting', {
    state: () => {
        return{
            mode: playerSettingInfo.mode || 0, // 012(Buttombox.vue)
            // playerSettingInfo.volume  null  undefined .8
            //
            volume: playerSettingInfo.volume ?? .8, // 
            lock:playerSettingInfo.lock || false, //
            themeMode: playerSettingInfo.themeMode, //
        }
    },
    getters: {
        getMode: (state) => {
            return state.mode;
        },
        getVolume: (state) => {
            return state.volume;
        },
        getLock: (state) =>{
            return state.lock;
        },
        getThemeMode: (state) =>{
            return state.themeMode;
        }
    },
    actions: {
        setPlayerSetting(type, val) {
            const info = JSON.parse(window.localStorage.getItem('player-setting')) || {};
            info[type] = val;//info
            window.localStorage.setItem('player-setting', JSON.stringify(info));
        }
    }

})