import utils from './util';

export default class Song{ //SongsongPHP

    //
    constructor( {
        id,
        name,
        mvId,
        singer,
        album,
        alia,
        duration,
        milliseconds,
        url,
        vip,
        license,
        publishTime
    }) {
        this.id = id
        this.name = name
        this.mvId = mvId
        this.singer = singer
        this.album = album
        this.alia = alia
        this.duration = duration
        this.milliseconds = milliseconds
        this.url = url
        this.vip = vip
        this.license = license
        this.publishTime = publishTime
    }
}

export function formatSongInfo (song) {
    return new Song({
        id: String(song.id),
        name: song.name,
        //,
        mvId: song.mv || song.mvid,
        singer: song.ar || song.artists,
        album: song.al || song.album,
        alia: song.alia || song.alias,
        vip: song.fee === 1,
        license: song.license,
        duration: utils.formatSongTime(song.dt || song.duration),
        milliseconds: (song.dt || song.duration) / 1000, // duration
        url: `https://music.163.com/song/media/outer/url?id=${song.id}.mp3`,
        publishTime: utils.formatTime(song.publishTime)
    })
}