import axios from "axios";
import store from '../store/index';


//

const instance = axios.create({  //axious 
    timeout: 1000 * 60, //
    baseURL:'https://api.trtst.com', //
    withCredentials: true, // cookitHTTPSSL

    validateStatus: function(states) { //HTTP code 
        return states >= 200 && states < 600 // axios 
    }
})

//:thencatch
axios.interceptors.request.use(function (config) {
    // 
    return config;
  }, function (error) {
    // 
    return Promise.reject(error);
  });


// 
axios.interceptors.response.use(function (response) {
    // 
    if(response.status == 301) //HTTP 
    {
        const accountStore = store.accountInfoStore();

        window.localStorage.removeItem('cookie');
        window.localStorage.removeItem('accountInfo');
        window.localStorage.removeItem('isLogin');
        accountStore.logout();
    }
    return response;
  }, function (error) {
    //  2xx 
    // 
    return Promise.reject(error);
});

  //

const ajaxMethod = ['get', 'post']
const  api = {}

ajaxMethod.forEach((method) => {
    //apigetpost
    api[method] = (uri, data, config) => {
        //Promise
        return new Promise( (resolve, reject) => {
            //instancegetpost
            instance[method](uri, data, config) //instanceinstance.method(uri, data, config)
            //
            .then(response => {
                resolve(response)
            })
            //
            .catch(error => {
                reject(error)
            })
        })
    } 
})


export default api 