import { formatSongInfo } from '../utils/song' 

export default {
    // 
        formartNum (val) {
        let num = 0;
        if (val > 99999999) {
            // 
            num = (val / 100000000).toFixed(2) + "";
        } else if (val > 9999) {
            // 
            num = (val / 10000).toFixed(2) + "";
        } else {
            num = val;
        }
        return num;
    },


    //   
        formartDate( originVal, dateRegul) {
            // https://blog.csdn.net/qq_50977450/article/details/121534539
            const date = new Date(originVal)
            const requlation = {
                yyyy:date.getFullYear(),
                MM:date.getMonth() + 1,
                dd:date.getDate(),
                HH:date.getHours(),
                mm:date.getMinutes(),
                ss:date.getSeconds()
            }
            for (const i in requlation) {
                const ret = new RegExp('('+ i +')').exec(dateRegul)//exec()  null
                if(ret) {
                    dateRegul = dateRegul.replace(ret[1], requlation[i])
                }
            }

            return dateRegul
        },
    // 
        formatSongs (list, privileges) {
            const ret = []
            list.map((item, index) => { //map()  
                if (item.id) {
                    // 
                    item.license = privileges && !privileges[index].cp
                    ret.push(formatSongInfo(item))
                }
            })
            return ret
        },


    //
        formatSongTime ( duration = 0 ) { //
            duration = duration? duration / 1000 : 0; //1 = 60000, 1000=60
            const m = (Math.floor(duration / 60) + '').padStart(2, '0');//01
            const s = (Math.floor(duration % 60) + '').padStart(2, '0');//00
            return `${m}:${s}`;//01:00
        },

    //
        formatTime(duration) {
            let date = '';
            const now = new Date();
            const past =  new Date(duration);

            //
            if(now.toDateString() === past.toDateString()) {
                date = this.formartDate(duration, 'HH:mm');
            //
            } else if( now.getFullYear() === past.getFullYear()) {
                date = this.formartDate(duration, 'MMdd HH:mm');
            //,
            } else {
                date = this.formartDate(duration, 'yyyyMMdd');
            }

            return date
        },

    // 
        concatPlayList (newList = [], oldList =[] ) { //
            const arr = [...oldList, ...newList]; // 
            const map = new Map();
            
            //arr
            for(const item of arr ) {
                //map
                if(!map.has(item.id)) { 
                    map.set(item.id, item)
                }
            }
            
            return [...map.values()] // map
        }
}